@import url(http://fonts.googleapis.com/earlyaccess/jejuhallasan.css);
@import url(https://fonts.googleapis.com/css?family=Black+Han+Sans:400);
body, h1 {
  font-family: "Raleway", sans-serif; }

body, html {
  background-color: darkgreen;
  height: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

::-webkit-scrollbar {
  display: none; }

.container {
  flex: 80% 1;
  display: flex;
  flex-direction: column;
  height: 100vh;
  /* We're calling this animation "crawl" */ }
  .container section {
    display: flex;
    flex-direction: column;
    flex: 1 0 100vh; }
  .container .bgimg {
    height: 143.375vw;
    flex: 1 0 143.375vw;
    background-position: center;
    background-size: cover;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-top: 5px;
    margin-left: 5px;
    width: calc(100vw - 10px); }
  .container .logo {
    width: 3em;
    height: 3em;
    margin-left: auto;
    margin-right: 1em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .container .carousel {
    flex: 1 0 100vh;
    justify-content: center;
    background: black;
    margin-left: 5px;
    width: calc(100% - 10px); }
  .container .mySlides {
    max-width: 100%;
    max-height: 100%; }
  .container .map-container {
    width: 100%;
    min-height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .container .map-text {
    overflow-y: scroll;
    text-align: center;
    background: darkred;
    width: calc(100% - 10px);
    height: calc(50% - 10px); }
    .container .map-text .shadow {
      font-family: 'Black Han Sans', serif;
      -webkit-transition: all 0.12s ease-out;
      transition: all 0.12s ease-out; }
      .container .map-text .shadow:hover {
        position: relative;
        top: -3px;
        left: -3px; }
    .container .map-text h1 {
      color: yellow;
      font-size: 8vw;
      text-shadow: 1px 1px black,  2px 2px black,  3px 3px black; }
      .container .map-text h1:hover {
        text-shadow: 1px 1px black,  2px 2px black,  3px 3px black,  4px 4px black,  5px 5px black,  6px 6px black; }
    .container .map-text p {
      color: ivory;
      font-size: 5vw;
      text-shadow: 1px 1px #165B33,  2px 2px #165B33,  3px 3px #165B33; }
      .container .map-text p:hover {
        text-shadow: 1px 1px #165B33,  2px 2px #165B33,  3px 3px #165B33,  4px 4px #165B33,  5px 5px #165B33,  6px 6px #165B33; }
    @media (orientation: landscape) {
      .container .map-text h1 {
        font-size: 4vw; }
      .container .map-text p {
        font-size: 2.5vw; } }
  .container .map-content {
    width: calc(100% - 10px);
    height: calc(50% - 10px); }
  @media (orientation: landscape) {
    .container .map-container {
      flex-direction: row; }
    .container .map-content {
      width: calc(50% - 10px);
      height: calc(100% - 10px); }
    .container .map-text {
      width: calc(50% - 10px);
      height: calc(100% - 10px); } }

@-webkit-keyframes crawl {
  0% {
    /* The element starts below the screen */
    top: 1300px;
    /* Rotate the text 20 degrees but keep it close to the viewer */
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -3000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

@keyframes crawl {
  0% {
    /* The element starts below the screen */
    top: 1300px;
    /* Rotate the text 20 degrees but keep it close to the viewer */
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -3000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

